@import "../../styles/variables";
.joke-section {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.load-more {
  display: flex;
  justify-content: space-around;
  .view-more {
    background-color: $brown;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
  }
}
